@import "@theme/vars.scss";

.banner {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color: $blue;
    text-decoration: none;
    color: #fff;
    height: 450px;
    width: 100%;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;

    @media only screen and (max-width: $bp-lg) {
        height: 350px;
        margin-right: -20px;
        margin-left: -20px;
        min-width: 100%;
        width: auto;
    }

    @media only screen and (max-width: $bp-md) {
        height: 300px;
    }

    &:hover{
        &::before{
            opacity: 0.45;            
        }
    }

    &::before{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.24) 60.94%), rgba(19, 19, 19, 0.6);
        opacity: 0;
        transition: opacity 0.5s ease;
    }

    &:hover &__img{
        transform: scale(102%);
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        will-change: transform;
        transition: transform 0.75s ease;
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        width: 100%;
        padding: 40px 56px;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;

        @media only screen and (max-width: $bp-xl) {
            padding: 32px 40px;
        }
    
        @media only screen and (max-width: $bp-lg) {
            padding: 24px;
        }
    }

    &__header{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap-reverse;
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        margin: 0 -4px 16px;
        max-width: 75%;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
        }
    }
  
    &__label {
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 32px;
        padding: 0 16px;
        // border-radius: 20px;
        border: 1px solid #E5E7EB;
        color: #E5E7EB;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        margin: 0 4px;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        transition: color 0.35s ease, border-color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            height: 24px;
            font-size: 14px;
            padding: 0 8px;
        }

        &:last-of-type{
            flex-shrink: 1;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;
        }
    }
  
    &__date {
        flex-shrink: 0;
        font-weight: 600;
        margin-bottom: 16px;
    }

    &__rate{
        font-size: 32px;
        line-height: 100%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
        }
    }

    &__subtitle{
        font-size: 24px;
        line-height: 100%;
        margin: 0 16px 16px 0;
        max-width: 50%;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
            max-width: 65%;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__title {
        font-weight: 600;
    }

    &__button{
        min-width: 200px;
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg) {
            min-width: auto;
        }
    }
}
