@import "@theme/vars.scss";

.grid{

    &__title{
        width: 100%;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
            line-height: 32px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__block{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 48px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__more{
        margin: 32px auto 0;
        font-size: 24px;
        
        background-color: transparent;
        color: #374151;
        fill: #374151;

        &[disabled]{
            pointer-events: none;
            opacity: 0.5;
        }

        &:hover{
            background-color: rgba(221, 205, 255, 0.1);
        }

        &:active{
            background-color: rgba(215, 199, 255, 0.5);
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }

    &__list{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-flow: dense;
        grid-gap: 16px;
    
        @media only screen and (max-width: $bp-md) {
            grid-template-columns: repeat(2, 1fr);
        }

        .banner{
            grid-column: span 4;
            margin-top: 40px;
            margin-bottom: 40px;
    
            @media only screen and (max-width: $bp-lg) {
                margin-top: 24px;
                margin-bottom: 24px;
            }
    
            @media only screen and (max-width: $bp-md) {
                margin-top: 16px;
                margin-bottom: 16px;
                grid-column: span 2;
            }
        }
    
        .card{
    
            &--lg{
                grid-column: span 2;
                grid-row: span 2;
    
                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 4;
                }
    
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2; 
                }
            }
    
            &--md{
                grid-row: span 2;
                grid-column: span 1;
    
                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                }
    
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2; 
                }
            }
    
            &--sm{
                grid-row: span 1;
                grid-column: span 1; 
    
                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                }
    
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2; 
                }
            }
    
            &--long{
                grid-column: span 4;
                
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2;
                }
            }
    
            &--line{
                grid-column: span 4;
                
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2;
                }
            }
        }
    
        .card-set{
            @media only screen and (max-width: $bp-lg) {
                grid-column: span 2;
            }
        }
    
        .card-event{
            grid-column: span 4;
    
            @media only screen and (max-width: $bp-md) {
                grid-column: span 2;
            }
        }
    }

    

    &--articles &__list{
        .card{
            &--lg{
                grid-column: 2 / 4;
                grid-row: span 3;

                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                    grid-row: span 2;
                }

                @media only screen and (min-width: $bp-md + 1) {
                    height: auto;
                }
            }

            &--sm{
                @media only screen and (max-width: $bp-lg) {
                    display: none;
                    grid-column: span 2;

                    &:nth-of-type(2),
                    &:nth-of-type(3) {
                        display: flex;
                    }
                }
            }
        }
    }

    &--news{
        
    }

    &--events &__list{
        row-gap: 24px;

        @media only screen and (max-width: $bp-lg) {
            row-gap: 16px;
        }
    }
}

