@import "@theme/vars.scss";

html {
  font-family: $defaultFontFamily;
  font-size: $defaultFontSize;
  line-height: $defaultLineHeight;
  color: $blue-dark;
  background-color: #FFF;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;

    &._lock {
        overflow: hidden;
    }
}

*{
    -webkit-tap-highlight-color: transparent;
    scrollbar-color: $blue #DEE5E8;
    scrollbar-width: thin;
    
    &::-webkit-scrollbar {
        height: 8px;
        width: 8px;
    }

    &::-webkit-scrollbar-track {
        background-color: $light;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $blue;
    }
}

img{
  image-rendering: -webkit-optimize-contrast;
}

.main{
    flex-grow: 1;
    width: 100%;

    // .breadcrumbs + section{
    //     padding-top: 0;
    // }

    section,
    article {
        overflow-x: hidden;

        &:first-child{
            padding-top: 56px;

            @media only screen and (max-width: $bp-lg) {
                padding-top: 48px;
            }
        }

        &:last-child{
            padding-bottom: 56px;

            @media only screen and (max-width: $bp-lg) {
                padding-bottom: 48px;
            }
        }
    }
}

.section{
    box-sizing: border-box;
    padding-bottom: 56px;

    @media only screen and (max-width: $bp-lg) {
        padding-bottom: 48px;
    }

    &--top{
        padding-top: 50px;

        @media only screen and (max-width: $bp-xxl) {
            padding-top: 40px;
        }

        @media only screen and (max-width: $bp-xl) {
            padding-top: 32px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-top: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-top: 16px;
        }
    }

    &__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 32px;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-wrap: wrap;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__filter{
        margin-top: 24px;
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__header &__filter {
        margin: 0;
        flex-grow: 0;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-top: 24px;
        }

        &>form{
            flex-grow: 0;

            @media only screen and (max-width: $bp-md) {
                flex-grow: 1;
            }
        }
    }
    
    &__title{
        box-sizing: border-box;
        color: $blue;
        margin-right: auto;
        padding-right: 16px;

        @media only screen and (max-width: $bp-lg) {
            max-width: calc(100% - 32px);
            padding-right: 8px;
        }

        @media only screen and (max-width: $bp-md) {
            padding-right: 0;
        }
    }

    &__link{
        font-weight: 600;
        font-size: 24px;
        line-height: 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 8px;
        }

        span{
            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
        
        .icon{
            @media only screen and (max-width: $bp-lg) {
                width: 16px;
                height: 16px;
                margin-left: 0 !important;
            }
        }
    }

    &__subtitle {
        margin-top: 32px;
    }
    
    &__badges{
        box-sizing: border-box;
        margin-top: 20px;

        margin-right: -72px;
        padding-left: 72px;
        margin-left: -72px;
        padding-right: 72px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: -40px;
            padding-left: 40px;
            margin-left: -40px;
            padding-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 15px;
            margin-right: -25px;
            padding-left: 25px;
            margin-left: -25px;
            padding-right: 25px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: -20px;
            padding-left: 20px;
            margin-left: -20px;
            padding-right: 20px;
        }


        -webkit-tap-highlight-color: transparent;
        scrollbar-color: transparent transparent;
        scrollbar-width: thin;
        
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }
    }

    &__inner{
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 1;
    }

}


.container {
    box-sizing: border-box;
    width: 100%;
    max-width: 1280px;
    padding: 0 20px;
    margin: 0 auto;

    @media only screen and (max-width: $bp-lg) {
        padding: 0 16px;
    }
}