@import "@theme/vars.scss";

.card-event {
    position: relative;
    box-sizing: border-box;
    display: flex;
    padding: 24px;
    background-color: $light;
    text-decoration: none;
    color: $dark;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;

    @media only screen and (max-width: $bp-lg) {
        padding: 16px;
    }

    &__img-wrapper:hover img{
        transform: scale(103%);
    }

    &__img-wrapper{
        display: flex;
        width: 256px;
        height: 256px;
        flex-shrink: 0;
        margin-right: 24px;

        @media only screen and (max-width: $bp-xl) {
            width: 216px;
            height: 216px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 160px;
            height: 160px;
            margin-right: 16px;
        }
        
        @media only screen and (max-width: $bp-md) {
            width: 80px;
            height: 80px;
            position: absolute;
            top: 16px;
            left: 16px;
            margin-right: 8px;
        }
    }
 
    &__img {
        display: flex;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__inner {
        position: relative;
        z-index: 3;
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 100%;
        overflow: hidden;

        &>*:last-child{
            margin-bottom: 0;
        }
    }

    &__content{
        width: 100%;
    }

    &__header{
        display: flex;
        flex-direction: column;
        text-decoration: none;
        color: $dark;
        margin-bottom: 16px;
        transition: color 0.35s ease;
        
        @media only screen and (max-width: $bp-md) {
            justify-content: center;
            padding-left: 88px;
            min-height: 80px;
        }

        &:hover{
            color: $blue-light;
        }
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;
        flex-shrink: 0;
        margin-bottom: 4px;
        
        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            -webkit-line-clamp: 2;
        }
    }

    &__subtitle{
        display: inline-flex;
        font-weight: 400;
        font-size: 16px;
        line-height: 28px;
        color: #9CA3AF;
        
        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 14px;
        }
    }

    &__list{
        display: flex;
        flex-direction: column;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 0;
        }
    }

    &__row{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -6px;
        
        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: flex-start;
        }

        &:last-of-type{
            margin-bottom: -8px;
        }
    }

    &__item{
        box-sizing: border-box;
        max-width: 100%;
        display: inline-flex;
        align-items: center;
        padding: 0 6px;
        margin-bottom: 8px;
        text-decoration: none;  
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        fill: $orange;
        color: $dark-light;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 22px;
        }

        @media only screen and (max-width: $bp-md) {
            overflow: hidden;
            white-space: nowrap;
        }

        &:hover{
            color: $blue-light;
        }

        & > .icon{
            width: 16px;
            height: 16px;
            margin-right: 12px;

            @media only screen and (max-width: $bp-md) {
                margin-right: 8px;
            }
        }

        & > .icon + span{

            @media only screen and (max-width: $bp-lg) {
                margin-left: 0;
            }
        }

        & > span{

            @media only screen and (max-width: $bp-md) {
                margin-left: 24px;
                max-width: 100%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .button{
            padding: 0 8px;

            @media only screen and (max-width: $bp-lg) {
                height: 24px;
                padding: 0 6px;
            }
        }
        
        &::after{
            content: "";
            display: inline-flex;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: #C9C9C9;
            margin-left: 12px;

            @media only screen and (max-width: $bp-md) {
                content: none;
            }
        }

        &:last-of-type::after{
            content: none;
        }
    }

    &__text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 2;
        font-family: 'Literata';
        font-size: 16px;
        line-height: 24px;
        color: $dark;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }


    .like{
        left: 0;
        top: 50%;
        transform: translateY(-50%);

        @media only screen and (max-width: $bp-md) {
            top: 40px;
            transform: none;
        }
    }

    &--sm .like {
        @media only screen and (max-width: $bp-md) {
            top: 50%;
            transform: translateY(-50%);
        }
    }


    &--md {
        background-color: #fff;
        padding: 16px;
    }

    &--md &__img-wrapper{
        width: 216px;
        height: 216px;

        @media only screen and (max-width: $bp-lg) {
            width: 160px;
            height: 160px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 80px;
            height: 80px;
            position: absolute;
            top: 16px;
            left: 16px;
        }
    }


    &--md &__title{
        font-size: 18px;
        line-height: 24px;

    }

    &--md &__subtitle{
        font-size: 14px;
        line-height: 100%;
    }

    &--md &__list{

    }

    &--md &__row{

    }
    &--md &__item{
        font-size: 14px;
        line-height: 22px;

        .button{
            height: 24px;
            font-size: 14px;
            padding: 0 6px;
        }
    }

    &--md &__text{
        font-size: 14px;
        line-height: 20px;
    }
    // &--md &__


    &--sm{
        padding: 8px;
        background-color: #fff;
        color: $gray-blue;

        &::before{
            content: none;
        }
    }

    &--sm &__img-wrapper{
        width: 72px;
        height: 72px;

        @media only screen and (max-width: $bp-md) {
            position: static;
        }
    }


    &--sm &__header{
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-md) {
            padding-left: 0;
            min-height: auto;
            margin-bottom: 4px;
        }
    }

    &--sm &__title{
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 4px;
        -webkit-line-clamp: 2;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 0;
        }
    }

    &--sm &__subtitle{
        font-size: 14px;
        line-height: 100%;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &--sm &__list{
        margin-bottom: 0;
    }

    &--sm &__item{
        font-size: 14px;
        line-height: 22px;
        overflow: hidden;
        white-space: nowrap;

        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &--sm &__date{
        margin-left: auto;
        padding-left: 4px;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
    }

    &--sm &__text{
        // -webkit-line-clamp: 2;
        font-size: 14px;
        line-height: 22px;

        max-height: 88px;
    }
    
}
