@import "@theme/vars.scss";


.like{
    box-sizing: border-box;
    cursor: pointer;
    position: absolute;
    z-index: 6;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 16px;
    fill: rgba(255, 255, 255, 0.5);
    --icon-opacity: 1;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-width: 1px 1px 1px 0;
    background-color: rgba(236, 125, 66, 0.15);
    backdrop-filter: blur(5px);
    transition: border-color 0.35s ease, background-color 0.35s ease, fill 0.35s ease, opacity 0.35s ease, padding 0.35s ease;

    &:hover{
        padding-left: 24px;
        fill: #fff;
        background-color: rgba(236, 125, 66, 0.5);
        border-color: rgba(236, 125, 66, 0.5);

    }

    &:active{
        background-color: #EC7D42;
        border-color: #EC7D42;
    }

    &__icon{

    }

    &.is-active{
        background-color: #EC7D42;
        border-color: #EC7D42;
        fill: #fff;

        &:hover{
            padding-left: 24px;
            background-color: #D3682E;
            border-color: #D3682E;
        }
    
        &:active{
            background-color: #BA531A;
            border-color: #BA531A;
        }
    }

    &.is-active &__icon{

    }


}