@import "@theme/vars.scss";

.card-set {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 24px;
    text-decoration: none;
    color: #FAFAFA;
    min-height: 210px;
    padding: 24px;
    overflow: hidden;

    @media only screen and (max-width: $bp-lg) {
        height: 160px;
    }

    &--dark{
        color: #1F1F1F;
    }

    &:hover{
     
    }

    &:hover &__img{
        transform: scale(103%);
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        will-change: transform;
        transition: transform 0.5s ease;
    }

    &__inner{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        position: relative;
        z-index: 2;
        flex-grow: 1;
        height: 100%;
    }

    &__subtitle {
        line-height: 100%;
        margin-bottom: 8px;
    }

    &__title {
        font-size: 28px;
        line-height: 100%;

        @media only screen and (max-width: $bp-xl) {
            font-size: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
        }
    }
}