@import "@theme/vars.scss";


.article {
    overflow: initial !important;

    &__inner {
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__actions {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        width: calc(100% + 16px);
        margin: 16px -8px 0;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 8px;
        }
    }

    &__action {
        margin: 8px 8px 0;

        @media only screen and (max-width: $bp-lg) {
            flex-grow: 1;
        }
        
    }



    &__body {
        margin-top: 32px;
        width: calc(100% - 442px);

        @media only screen and (max-width: $bp-xxl) {
            width: calc(100% - 400px);
        }

        @media only screen and (max-width: $bp-xl) {
            width: calc(100% - 350px);
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }

        & > *{
            &:first-child{
                margin-top: 0;
            }
        }
    }

        &__grid{
            padding-top: 24px;
            margin: 24px 0;
            border-top: 1px solid #D1D5DB;

            &:first-child{
                padding-top: 0;
                border-top: 0;
                margin-top: 0;

                @media only screen and (max-width: $bp-lg) {
                    padding-top: 8px;
                }
            }

            .grid{

                &__title{
                    margin-bottom: 16px;
                    font-size: 24px;
                    line-height: 32px;
                
                    @media only screen and (max-width: $bp-lg) {
                        font-size: 22px;
                        line-height: 27px;
                    }
                
                    @media only screen and (max-width: $bp-md) {
                        font-size: 20px;
                        line-height: 25px;
                    }
                }

                &__block{
                    padding-bottom: 24px;
                    margin-bottom: 24px;
                    border-bottom: 1px solid #D1D5DB;
                }

                &__more{
                    margin-top: 16px;
                    width: 100%;
                    font-size: 14px;
                    height: 40px;

                    background-color: $light;
                    color: $dark-light;
                    fill: $dark-light;
                    border: 1px solid #E5E7EB;
            
                    &:hover{
                        background-color: $light-light;
                    }
            
                    &:active{
                        background-color: #E5E7EB;
                        border-color: #D1D5DB;
                    }

                    .button{
                        &__icon {
                            width: 16px;
                            height: 16px;
                        }
                    }

                }
            }
            
        }

        &__content {
            font-family: 'Literata';
            color: $dark-light;

            &>*{
                &:first-child{
                    margin-top: 0;
                }

                &:last-child{
                    margin-bottom: 0;
                }
            }

            h1, h2, h3, h4, h5 {
                font-weight: 600;
            }

            h2{
                font-size: 24px;
                line-height: 32px;
                margin-bottom: 32px;

                @media only screen and (max-width: $bp-lg) {
                    font-size: 22px;
                    line-height: 27px;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 18px;
                    line-height: 24px;
                }
            }

            h3{
                font-size: 20px;
                line-height: 135%;
            
                @media only screen and (max-width: $bp-lg) {
                    font-size: 18px;
                }
            
                @media only screen and (max-width: $bp-md) {
                    font-size: 16px;
                }
            }

            p{
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 24px;
                
                @media only screen and (max-width: $bp-lg) {
                    font-size: 14px;
                    line-height: 22px;

                }

                &:last-child{
                    margin-bottom: 0;
                }

                span{
                    font-weight: 600;
                }

                a{

                }
            }

            figure{
                box-sizing: border-box;
                position: relative;
                margin: 56px 0;
                padding: 32px 96px;
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                background-size: 100%;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 797 128' fill='none' xmlns='http://www.w3.org/2000/svg' preserveAspectRatio='none'%3E%3Cpath d='M49.7334 0L797 8L767.945 112L0 128L49.7334 0Z' fill='%23151F6D'/%3E%3C/svg%3E%0A");
                font-family: 'Mont';
                text-align: center;

                @media only screen and (max-width: $bp-lg) {
                    padding: 40px 32px;
                }

                &::before{
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 96px;
                    height: 96px;
                    z-index: 1;
                    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 96 96' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cmask id='mask0_407_68903' style='mask-type:alpha' maskUnits='userSpaceOnUse' x='0' y='0' width='96' height='96'%3E%3Crect width='96' height='96' fill='%23D9D9D9'/%3E%3C/mask%3E%3Cg mask='url(%23mask0_407_68903)'%3E%3Cpath d='M60.5 71L68.5 55H52.5V27H80.5V55L72.5 71H60.5ZM24.5 71L32.5 55H16.5V27H44.5V55L36.5 71H24.5Z' fill='%233E3C8F'/%3E%3C/g%3E%3C/svg%3E%0A");

                    @media only screen and (max-width: $bp-lg) {
                        width: 64px;
                        height: 64px;
                    }
                }


                @media only screen and (max-width: $bp-lg) {
                    margin: 40px 0;
                }

                blockquote{
                    position: relative;
                    z-index: 2;
                    color: #fff;
                    margin: 0;
    
                    p{
                        font-weight: 600;
                        font-size: 18px;
                        line-height: 26px;
                        font-style: italic;

                        @media only screen and (max-width: $bp-lg) {
                            font-size: 16px;
                            line-height: 24px;
                        }
                    }
                }

                figcaption{
                    position: relative;
                    z-index: 2;
                    margin: 16px 0 0;
                    color: #D1D5DB;

                    p{
                        font-size: 14px;
                        line-height: 22px;
                    }
                }
            }

            ul{
                margin: 32px 0;

                li{
                    margin-bottom: 24px;
                    
                    &:last-of-type{
                        margin-bottom: 0;
                    }

                    p{

                    }
                }
            }

            ol{
                margin: 32px 0;
                
                li{
                    margin-bottom: 24px;

                    &::marker{
                        font-family: 'Mont';
                        font-weight: 600;
                    }
                    p{

                    }
                }
            }
        }

            &__border {
                margin: 32px 0;
                padding: 32px;
                border: 1px solid #D1D5DB;
                display: flex;
                flex-direction: column;
                font-family: 'Mont';
                color: #1F2937;

                @media only screen and (max-width: $bp-xl) {
                    padding: 24px;
                }

                @media only screen and (max-width: $bp-lg) {
                    padding: 16px;
                    margin: 24px 0;
                }

                p{
                    
                }
            }

            &__images {
                display: flex;
                margin: 32px 0;

                @media only screen and (max-width: $bp-md) {
                    flex-direction: column;
                }
            }

            &__images &__img{
                margin: 0 16px 0 0;

                @media only screen and (max-width: $bp-md) {
                    margin-right: 0;
                    margin-bottom: 16px;
                }

                &:last-of-type{
                    margin-right: 0;

                    @media only screen and (max-width: $bp-md) {
                        margin-bottom: 0;
                    }
                }
            }

            &__img {
                display: flex;
                overflow: hidden;
                width: 100%;
                flex-grow: 1;
                height: 400px;
                margin: 32px 0;

                @media only screen and (max-width: $bp-lg) {
                    height: 300px;
                }

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &__slider {
                margin: 24px 0 40px;
                
                @media only screen and (max-width: $bp-lg) {
                    margin: 32px 0;
                }

                .slider {

                    &__inner {
                    }
            
                    &__wrapper {
                    }
            
                    &__slide {
                    }
            
                    &__navigation {
                        position: absolute;
                        right: 32px;
                        bottom: 32px;

                        @media only screen and (max-width: $bp-lg) {
                            display: none;
                        }
                    }
            
                    &__button {
                    }
            
                    &__button--prev {
                    }
            
                    &__button-icon {
                    }
            
                    &__button--next {
                    }
            
                    &__pagination {
                    }
                }
            }

            &__slider &__img{
                margin: 0;
            }

        
        &__author-block {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-top: 24px;
            padding: 24px 0;
            border: 1px solid #D1D5DB;
            border-width: 1px 0;
            transition: background-color 0.35s ease;

        }

        &__author-title {
            font-size: 16px;
            line-height: 100%;
            color: $light-medium;
            margin-bottom: 8px;
        }

        &__author {
            cursor: pointer;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            padding: 8px;
            text-decoration: none;
            font-weight: 600;
            color: $dark;
            transition: background-color 0.35s ease;

            &:hover{
                background-color: #E5E7EB;
            }

            span{
                font-size: 16px;
                line-height: 100%;
            }
        }

        &__author-img {
            display: flex;
            width: 32px;
            height: 32px;
            flex-shrink: 0;
            border-radius: 50%;
            margin-right: 12px;
            overflow: hidden;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__comments {
            margin-top: 24px;
            width: 100%;
        }

    &__aside {
        display: block;
        width: 402px;
        flex-shrink: 0;
        margin-left: auto;
        margin-top: 32px;

        @media only screen and (max-width: $bp-xxl) {
            width: 360px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 320px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-top: 24px;
        }
    }

        &__aside-inner {
            display: flex;
            flex-direction: column;
            position: sticky;
            top: 172px;
            background-color: #fff;
            max-height: calc(100vh - 200px);

            @media only screen and (max-width: $bp-lg) {
                max-height: none;
                position: relative;
                background-color: transparent;
                top: 0;
            }
        }

        &__aside-title {
            font-weight: 600;
            color: $dark-light;
            padding-bottom: 12px;
            margin-bottom: 12px;
            width: 100%;
            position: sticky;
            top: 0px;
            z-index: 4;
            background-color: #fff;

            @media only screen and (max-width: $bp-lg) {
                position: static;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 18px;
            }
        }

        &__aside-list {
            display: flex;
            flex-wrap: wrap;
            overflow-y: auto;

            @media only screen and (max-width: $bp-lg) {
                overflow: hidden;
            }

            .card{
                width: 100%;
                margin-bottom: 16px;

                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        }

        &__aside-button {
            margin-top: 16px;
            width: 100%;
            justify-content: space-between;
            z-index: 5;
            flex-shrink: 0;
        }

    &--places {
        .article-intro{
            min-height: auto;
            padding: 0;
            position: static;
            display: flex;
            flex-direction: column;
    
            &::after{
                content: none;
            }

            &__main{
                width: calc(100% - 442px);
                color: $blue;

                @media only screen and (max-width: $bp-xxl) {
                    width: calc(100% - 400px);
                }

                @media only screen and (max-width: $bp-xl) {
                    width: calc(100% - 350px);
                }

                @media only screen and (max-width: $bp-lg) {
                    width: 100%;
                    margin-top: 24px;
                }
            }

            &__slider{
                width: calc(100% - 442px);

                @media only screen and (max-width: $bp-xxl) {
                    width: calc(100% - 400px);
                }

                @media only screen and (max-width: $bp-xl) {
                    width: calc(100% - 350px);
                }

                @media only screen and (max-width: $bp-lg) {
                    order: 2;
                    min-width: 100%;
                    width: auto;
                }
            }
        
            &__title{
                font-size: 34px;
                line-height: 125%;
                
                @media only screen and (max-width: $bp-lg) {
                    font-size: 30px;
                }
            
                @media only screen and (max-width: $bp-md) {
                    font-size: 24px;
                    line-height: 32px;
                }
            }
        
            &__subtitle{
                font-weight: 400;
                font-size: 20px;
                line-height: 32px;
                margin-top: 10px;
                color: #4B5563;
        
                @media only screen and (max-width: $bp-lg) {
                    font-size: 18px;
                    line-height: 24px;
                    margin-top: 8px;
                }
            }
   
        }

        .article-info{
            padding-top: 116px;
            position: absolute;
            height: 100%;
            top: 0;
            right: 0;
            color: $dark;
            background-color: transparent;
            width: 402px;
            backdrop-filter: none;

            @media only screen and (max-width: $bp-xxl) {
                width: 360px;
            }
    
            @media only screen and (max-width: $bp-xl) {
                width: 320px;
            }
    
            @media only screen and (max-width: $bp-lg) {
                position: relative;
                padding-top: 0;
                width: 100%;
                height: auto;
                margin-top: 24px;
                background-color: $light-light;
            }

            &__inner{
                overflow: visible;

                @media only screen and (max-width: $bp-lg) {
                    overflow: hidden;
                }
            }

            &__content{
                position: sticky;
                top: 160px;
                border-left: 1px solid #D1D5DB;
                max-height: calc(100vh - 160px);
                overflow-y: auto;

                @media only screen and (max-width: $bp-lg) {
                    max-height: none;
                    overflow: initial;
                    position: static;
                    border-left: 0;
                }
            }

            &__block-title{
                color: #9CA3AF;
            }

            &__item{
                color: #1F2937;
                fill: #1F2937;

                &.link{
                    &:hover{
                        color: $blue-medium;
                        fill: $blue-medium;
                    }
                }
            }

            &__schedule{
                color: #6B7280;

                &:hover{
                    color: $dark;
                }
            }

            &__button{
                color: $dark;
                fill: $dark;
            }
        }
    }

    &--places &__inner > &__actions{
        position: absolute;
        top: 0;
        right: 0;
        width: 418px;
        z-index: 5;
        margin-top: 0;
        
        @media only screen and (max-width: $bp-xxl) {
            width: 376px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 336px;
        }

        @media only screen and (max-width: $bp-lg) {
            position: static;
            width: calc(100% + 16px);
            margin-top: 16px;
        }
    }

    &--places &__inner > &__actions &__action{
        flex-grow: 1;
    }
}

.article-intro{
    box-sizing: border-box;
    position: relative;
    display: flex;
    width: 100%;
    min-height: 400px;
    padding: 30px;
    color: #fff;

    @media only screen and (max-width: $bp-lg) {
        min-height: auto;
        padding: 0;
        color: $blue;
        flex-direction: column;
    }

    &::after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        background-color: rgba(17, 24, 39, 0.5);

        @media only screen and (max-width: $bp-lg) {
            content: none;
        }
    }
    
    &__img {
        display: flex;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        @media only screen and (max-width: $bp-lg) {
            position: static;
            order: 2;
            height: 300px;
            margin-top: 24px;
            width: auto;
            min-width: 100%;
            margin: 24px -16px 0;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__slider{
        width: 100%;
        margin-top: 32px;
        
        @media only screen and (max-width: $bp-lg) {
            position: static;
            order: 2;
            margin-top: 24px;
            width: auto;
            min-width: 100%;
            margin: 24px -16px 0;
        }

        .slider{
            &__navigation {
                position: absolute;
                right: 32px;
                bottom: 32px;

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }
        }
    }

    &__slider &__img{
        position: static;
        margin: 0;

        @media only screen and (max-width: $bp-lg) {
            height: 300px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 268px;
        }
    }

    &__main{
        position: relative;
        z-index: 2;
        height: 100%;
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-lg) {
            order: 1;
            height: auto;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        flex-wrap: wrap-reverse;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            order: 2;
            flex-wrap: wrap;
            margin: 12px 0 0;
        }
    }

    &__labels {
        display: flex;
        align-items: center;
        margin-right: 8px;
    }

    &__label {
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 32px;
        padding: 0 8px;
        border: 1px solid #fff;
        text-decoration: none;
        color: #fff;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        margin-right: 8px;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        transition: color 0.35s ease, border-color 0.35s ease;
        
        @media only screen and (max-width: $bp-lg) {
            height: 24px;
            padding: 0 6px;
            color: $orange;
            border-color: $orange;
        }

        &:last-of-type,
        &:nth-last-child(2){
            flex-shrink: 1;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;
        }
    }

    &__date {

        @media only screen and (max-width: $bp-lg) {
            color: #6B7280;
            font-size: 14px;
            line-height: 22px;
        }
    }

    &__labels + &__date{
        @media only screen and (max-width: $bp-lg) {
            margin-top: 12px;
            padding-top: 12px;
            width: 100%;
            border-top: 1px solid #D1D5DB;
        }
    }

    &__title {
        margin-top: auto;

        @media only screen and (max-width: $bp-lg) {
            color: $blue;
            margin: 0;
        }
    }

    &__subtitle {
        margin-top: 16px;
        color: #D1D5DB;

        @media only screen and (max-width: $bp-lg) {
            margin: 8px 0 0;
            font-weight: 400;
            color: $blue;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__info{
        width: 320px;
        margin-left: 48px;

        @media only screen and (max-width: $bp-lg) {
            order: 3;
            margin: 0;
            width: 100%;
            margin-top: 16px;

        }
    }
}

.article-info{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100%;
    flex-shrink: 0;
    background: rgba(243, 244, 246, 0.2);
    backdrop-filter: blur(5px);
    color: $light;
    position: relative;
    z-index: 3;

    @media only screen and (max-width: $bp-lg) {
        height: auto;
        color: $dark;
        background-color: $light-light;
    }

    &__inner{
        display: flex;
        flex-direction: column;
        height: 100%;
        position: relative;
        overflow: hidden;
        transition: max-height 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            max-height: 218px;
        }

             
        &::after{
            display: block;
            width: 100%;
            height: calc(100% - 90px);
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;
            background: linear-gradient(180deg, rgba(243, 244, 246, 0) -80.56%, rgba(243, 244, 246, 0.1) -80.54%, #F3F4F6 100%);
            transition: opacity 0.35s ease;

            @media only screen and (max-width: $bp-lg) {
                content: "";
            }
        }

        &.is-active::after{
            opacity: 0;
            pointer-events: none;
        }
    }

    &__title {
        margin-bottom: 24px;
        
        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__content {
        padding: 24px;

        @media only screen and (max-width: $bp-lg) {
            padding: 16px;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        margin-bottom: 24px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__block-title {
        margin-bottom: 12px;
        color: #D1D5DB;
        font-size: 16px;
        line-height: 100%;

        @media only screen and (max-width: $bp-lg) {
            color: $light-medium;
            margin-bottom: 8px;
        }
    }

    &__item {
        display: inline-flex;
        align-items: center;
        color: $light;
        fill: $light;
        text-decoration: none;
        font-weight: 600;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 12px;

        @media only screen and (max-width: $bp-lg) {
            color: $dark;
            fill: $dark;
            font-size: 16px;
            line-height: 28px;
            margin-bottom: 8px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }

        span{
            margin-left: 8px;
        }

        .icon{
            width: 20px;
            height: 20px;
        }
    }

    &__item + &__map{
        margin-top: 12px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 8px;
        }
    }

    &__map{
        height: 200px;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            height: 180px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 120px;
        }
    }

    &__profile {
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        text-decoration: none;
        padding: 8px;
        margin-bottom: 4px;

        &:last-of-type{
            margin-bottom: 8px;
        }
    }

    &__profile-img {
        display: flex;
        flex-shrink: 0;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 12px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__schedule{
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 28px;
        padding: 0 8px;
        margin-bottom: 12px;
        font-weight: 600;
        font-size: 18px;
        line-height: 24px;
        color: $light-light;
        transition: background-color 0.35s ease, color 0.35s ease;

        &:last-child{
            margin-bottom: 0;
        }

        &:hover{
            color: $light;
            background-color: #F3F4F6;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            line-height: 28px;
            margin-bottom: 8px;
        }
    }

    &__button {
        color: $light;
        fill: $light;
        background: rgba(249, 250, 251, 0.15);
        border: 1px solid rgba(229, 231, 235, 0.5);
        margin-top: 24px;

        @media only screen and (max-width: $bp-lg) {
            background-color: $light;
            color: $dark;
            fill: $dark;
            border-color: #E5E7EB;
        }

        &:hover{
            color: $dark;
            fill: $dark
        }
    }

    &__profile + &__button{
        margin-top: 0;
    }

    &__more{
        position: absolute;
        bottom: 16px;
        left: 0;
        margin: 0 16px;
        width: calc(100% - 32px);
        z-index: 2;
        display: none;

        @media only screen and (max-width: $bp-lg) {

            &.is-visible{
                display: flex;
            }

            &.is-active{
                pointer-events: none;
                opacity: 0;
            }
        }
    }
}
