@import "@theme/vars.scss";

.importantly {

    .container{
        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }
    }

    &__slider {

        .slider {

            &__inner {
                overflow: visible;
            }
        
            &__wrapper {
            }
        
            &__slide {
                opacity: 0.4;
                transition: opacity 0.5s ease 0.2s;

                &.swiper-slide-active{
                    opacity: 1;
                }
            }
        
            &__navigation {
                position: absolute;
                bottom: 40px;
                right: 56px;
                
            }
        
            &__button {
            }
        
            &__button--prev {
            }
        
            &__button-icon {
            }
        
            &__button--next {
            }
        
            &__pagination {
            }
        }
    }

    &__item {
        box-sizing: border-box;
        position: relative;
        cursor: pointer;
        display: block;
        width: 100%;
        height: 500px;
        padding: 40px 56px;
        text-decoration: none;
        color: $light;
        overflow: hidden;

        @media only screen and (max-width: $bp-xl) {
            padding: 32px 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 400px;
            padding: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 300px;
        }

        &::after{
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            opacity: 0.75;
            background: linear-gradient(0deg, rgba(17, 24, 39, 0.30), rgba(17, 24, 39, 0.30)), linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 100%);
            transition: opacity 0.5s ease;
        }

        &:hover::after{
            opacity: 1;
        }
    }

    &__item:hover &__img{
        transform: scale(102%);
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        will-change: transform;
        transition: transform 0.75s ease;
    }

    &__item-inner {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        height: 100%;
    }

    &__subtitle {
        font-weight: 600;
        margin-bottom: 16px;
    }

    &__main {

        @media only screen and (max-width: $bp-lg) {
            margin-top: auto;
        }

        &>*{
            &:last-child{
                margin-bottom: 0;
            }
        }
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__label {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 32px;
        padding: 0 14px;
        margin-bottom: 16px;
        margin-right: 10px;
        font-weight: 600;
        font-size: 14px;
        line-height: 14px;
        border-radius: 20px;
        color: $light;
        background-color: $orange;

        @media only screen and (max-width: $bp-lg) {
            height: 24px;
            padding: 0 6px; 
            margin-bottom: 8px;
            margin-right: 6px;
        }
    }

    &__title {
        margin-bottom: 16px;
        color: #ECECEC;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 8px;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__info-item {
        display: inline-flex;
        align-items: center;
        font-size: 16px;
        line-height: 16px;
        color: #F5F5F5;
        fill: #F5F5F5;

        .icon{
            width: 16px;
            height: 16px;
        }

        .icon + span{
            margin-left: 8px;
        }

        span + .icon{
            margin-left: 8px;
        }

        &::after{
            content: "";
            display: inline-flex;
            background-color: $light;
            width: 3px;
            height: 3px;
            border-radius: 50%;
            margin: 0 12px;
        }

        &:last-of-type::after{
            content: none;
        }
    }

    &__button {
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 24px;
        }
    }
}
