@import "@theme/vars.scss";

.search {
    position: relative;
    z-index: 10;
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    flex-grow: 1;
    height: 50px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    transition: padding 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        font-size: 16px;
        height: 38px;
        max-width: none;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 14px;
    }

    form{
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
    }

    &__field{
        outline: none;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding: 0 45px 0 20px;
        background-color: #E6E8F2;
        color: $dark-light;
        border: none !important;
        transition: background-color 0.35s ease,color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 36px 0 16px;
            background-color: $blue-medium;
            color: $dark-light;
        }

        &:focus{
            background-color: #D7C7FF;
        }

        &::placeholder{
            color: #6B7280;
            opacity: 1;

            @media only screen and (max-width: $bp-lg) {
                color: #fff;
            }
        }
    }

    &__button{
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $blue;
        fill: $light;
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        transition: background-color 0.35s ease, fill 0.35s ease;

        &:hover{
            background-color: $blue-dark;
        }
        
        @media only screen and (max-width: $bp-lg) {
            width: 16px;
            height: 16px;
            right: 16px;
            background-color: transparent;

            &:hover{
                fill: $blue;
                background-color: transparent;
            }
        }

    }

    &__icon{
        width: 16px;
        height: 16px;
    }

    &__dropdown{
        position: absolute;
        top: 100%;
        right: 0;
        width: 600px;
        opacity: 0; 
        visibility: hidden;
        transform: translateY(0);

        @media only screen and (max-width: $bp-xl) {
            width: 100%;
        }
    }

    .is-focus &__dropdown{
        opacity: 1; 
        visibility: visible;
        transform: translateY(8px);
    }
}

.search-dropdown {
    box-sizing: border-box;
    padding: 24px 0 16px;
    height: auto;
    background-color: #fff;
    border: 1px solid #D1D5DB;
    transition: opacity 0.4s ease, visibility 0.4s ease, transform 0.6s ease;

    &__inner {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__list {
        padding: 0 16px;
        overflow-y: auto;
        max-height: 500px;

        @media only screen and (max-width: $bp-lg) {
            max-height: 400px;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        margin-bottom: 8px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__block-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        min-height: 32px;
        position: sticky;
        top: 0;
        background-color: #fff;
        z-index: 10;
    }

    &__title {
     
    }

    &__link {
        padding: 8px 16px;
        font-size: 14px;    
        line-height: 14px;

        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &__block-list {
        display: flex;
        flex-direction: column;

        & > *{
            margin-bottom: 16px;

            &:last-child{
                margin-bottom: 0;
            }
        }
    }

    &__button {
        flex-grow: 1;
        margin: 0 16px;
        margin-top: 16px;
    }
}
